header {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    position: relative;
    width: 100%;
    height: 80px;
    left: 0px;
    background-color: #212121;
}

.logo {
    padding: 20px 60px;
}

.header-menu {
    display: flex;
    justify-content: space-between;
}

.key-menu {
    margin: auto;
}

a {
    text-decoration: none;
}

.main-key {
    font-family: Alegreya Sans;
    font-size: 18px;
    padding: 0px 20px;
    color: white;
}

.main-info {
    position: relative;
    letter-spacing: 0.05em;
    width: 100%;
    min-height: calc(100vh - 80px);
    left: 0px;

    background: linear-gradient(119.69deg, rgba(204, 204, 204, 0.37) 0%, rgba(102, 102, 102, 0.23) 100%);
}

// search form

.search {
    box-sizing: border-box;
    position: relative;
    width: 300px;
    margin: auto 20px;

&_impt {
    height: 20px;
    width: 0px;
    padding: 0 42px 0 15px;
    border: none;
    border-bottom: 2px solid transparent;
    outline: none;
    background: transparent;
    transition: .4s cubic-bezier(0, 0.8, 0, 1);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    }   
 
&_impt:focus {
    width: 250px;
    z-index: 1;
    border-bottom: 2px solid #F9F0DA;
    }  
}

.search-butt {
    cursor: pointer;
    border: none;
    background: #212121;
  }

.search-butt:before {
    // font-family: FontAwesome;
    // content: "\f002";
    font-size: 16px;
    color: #F9F0DA;
  }

//end search form

//start main-page block slider

.slider {
    height: calc(100vh - 80px);
    width: 100%;
    overflow: hidden;

&_container {
    transition: all .3s ease;
    transform: translateX(0px);
    display: flex;
    height: 100%;
    width: 100%;    
    }    
}

.slide {
    height: 100%;
    width: 100%;

    &:nth-child(2) {
    }

    &:nth-child(3) {
    }
}

.slide-dots {
    position: absolute;
    bottom: 20px;
    display: flex;
    justify-content: center;
    width: 100%;
}

.slide-dot {
    width: 48px;
    height: 4px;
    background-color: white;
    margin: 0px 5px;
    border-radius: 2px;

    &.active {
        background: rgba(71, 108, 0, 0.76);
    }
}

//end main-page block slider

